.Text {
    font-family: Orbitron;
    font-weight: bold;
    color: white;
    text-shadow: 2px 2px #000000;
  }

  .Button {
    color: white;
    background-color: #0074FF;
    border-radius: 6px;
    font-family: Helvetica Neue;
  }

  .Card {
    text-align: center;
    justify-content: center;
    align-items: center;
    background: linear-gradient(90deg, #FC466B 0%, #3F5EFB 100%);
    border-style: solid;
    border-radius: 5px;
    border-width: 1px;
    margin-left: auto;
    margin-right: auto;
    width: 40vh;
    height: 50vh;
  }

  .Flair {
    margin-top: 10px;
    margin-bottom: auto;
    margin-left: auto;
    margin-right: auto;
    border-style: solid;
    border-width: 1px;
    border-color: white;
    width: 35vh;
    height: 45vh;
    padding: 5px;
  }

  /* Smartphones (portrait and landscape) ----------- */
@media only screen
and (min-width : 320px)
and (max-width : 480px) {
/* Styles */
.Card {
  width: 50vh;
  height: 85vh;
}

.Flair {
  width: 45vh;
  height: 80vh;
}
}


 
/* Smartphones (landscape) ----------- */
@media only screen
and (min-width : 321px) {  
/* Styles */
.Card {
  width: 100%;
  height: 95vh;
}

.Flair {
  width: 90%;
  height: 85vh;
}

}
 
